import React, { Component } from 'react';
import {Dropdown} from 'react-bootstrap';
import {NavLink} from 'react-router-dom';
import ChatList from './ChatList';
import Aux from "../../../../../hoc/_Aux";
import DEMO from "../../../../../store/constant";


import avatar8 from '../../../../../assets/images/user/avatar-8.jpg';

import IntegrationScenarioNotif from '../../../../../assets/images/IntegrationScenarioNotif.svg';
import WorkflowNotif from '../../../../../assets/images/WorkflowNotif.svg';
import SystemNotif from '../../../../../assets/images/SystemNotif.svg';
import SecurityNotif from '../../../../../assets/images/SecurityNotif.svg';

class NavRight extends Component {
    state = {
        listOpen: false
    };

    render() {
        let navItemClass = ['nav-item'];
        if (this.props.windowWidth <= 575) {
            navItemClass = [...navItemClass, 'd-none'];
        }
        let dropdownRightAlign = false;
        if (this.props.rtlLayout) {
            dropdownRightAlign = true;
        }

        return (
            <Aux>
                <ul className="navbar-nav ml-auto">
                    <li className={navItemClass.join(' ')}>
                        <Dropdown alignRight={dropdownRightAlign}>
                            <Dropdown.Toggle variant={'link'} id="dropdown-basic">
                                Most used
                            </Dropdown.Toggle>
                            <ul>
                                <Dropdown.Menu>
                                    <li><a className="dropdown-item" href={DEMO.BLANK_LINK}>Workflow status pdf export</a></li>
                                    <li><a className="dropdown-item" href={DEMO.BLANK_LINK}>Add new user</a></li>
                                    <li><a className="dropdown-item" href={DEMO.BLANK_LINK}>WDtoPD log</a></li>
                                    <li><a className="dropdown-item" href={DEMO.BLANK_LINK}>Workflow events pdf export</a></li>
                                    <li><a className="dropdown-item" href={DEMO.BLANK_LINK}>SFtoPD NewHire log</a></li>
                                    <li><a className="dropdown-item" href={DEMO.BLANK_LINK}>Add new role</a></li>
                                </Dropdown.Menu>
                            </ul>
                        </Dropdown>
                    </li>
                    <li>
                        <Dropdown alignRight={!this.props.rtlLayout}>
                            <Dropdown.Toggle variant={'link'} id="dropdown-basic">
                                <i className="icon feather icon-bell"/>
                            </Dropdown.Toggle>
                            <Dropdown.Menu alignRight className="notification">
                                <div className="noti-head">
                                    <h6 className="d-inline-block m-b-0">Notifications</h6>
                                    <div className="float-right">
                                        <a href={DEMO.BLANK_LINK} className="m-r-10">mark as read</a>
                                        <a href={DEMO.BLANK_LINK}>clear all</a>
                                    </div>
                                </div>
                                <ul className="noti-body">
                                    <li className="n-title">
                                        <p className="m-b-0">NEW</p>
                                    </li>
                                    <li className="notification">
                                        <div className="media">
                                            <div className="mr-3 photo-table">
                                                <i className="fa fa-circle text-c-green f-10 m-r-10"/>
                                                <a><img className="rounded-circle" style={{width: '40px'}} src={IntegrationScenarioNotif} alt="chat-user"/></a>
                                            </div>
                                            <div className="media-body">
                                                <h6 className="d-inline-block">Jira - PeopleDoc / 13mn ago</h6>
                                                <p className="m-b-0 text-muted">Integration scenario SmartConnect - test successfully run!</p>
                                            </div>
                                        </div>
                                    </li>
                                    <li className="n-title">
                                        <p className="m-b-0">EARLIER</p>
                                    </li>
                                    <li className="notification">
                                    <div className="media">
                                            <div className="mr-3 photo-table">
                                                <i className="fa fa-circle text-c-green f-10 m-r-10"/>
                                                <a><img className="rounded-circle" style={{width: '40px'}} src={IntegrationScenarioNotif} alt="chat-user"/></a>
                                            </div>
                                            <div className="media-body">
                                                <h6 className="d-inline-block">Jira - PeopleDoc / 1h46mn ago</h6>
                                                <p className="m-b-0 text-muted">Integration scenario SmartConnect - prod - test successfully run!</p>
                                            </div>
                                        </div>
                                    </li>
                                    <li className="notification">
                                    <div className="media">
                                            <div className="mr-3 photo-table">
                                                <i className="fa fa-circle text-c-green f-10 m-r-10"/>
                                                <a><img className="rounded-circle" style={{width: '40px'}} src={WorkflowNotif} alt="chat-user"/></a>
                                            </div>
                                            <div className="media-body">
                                                <h6 className="d-inline-block">SAP HCM - SF / 4 hours ago</h6>
                                                <p className="m-b-0 text-muted">Integration scenario OrgStructureEurope - test successfully run!</p>
                                            </div>
                                        </div>
                                    </li>
                                </ul>
                                <div className="noti-footer">
                                <NavLink to="/message">show all</NavLink>
                                </div>
                            </Dropdown.Menu>
                        </Dropdown>
                    </li>
                    <li className={this.props.rtlLayout ? 'm-r-15' : 'm-l-15'}>
                        <a href={DEMO.BLANK_LINK} className="displayChatbox" onClick={() => {this.setState({listOpen: true});}}><i className="icon feather icon-mail"/></a>
                    </li>
                    <li>
                        <Dropdown alignRight={!this.props.rtlLayout} className="drp-user">
                            <Dropdown.Toggle variant={'link'} id="dropdown-basic">
                                <i className="icon feather icon-user"/>
                            </Dropdown.Toggle>
                            <Dropdown.Menu alignRight className="profile-notification">
                                <div className="pro-head">
                                    <img src={avatar8} className="img-radius" alt="User Profile"/>
                                    <span>Robert Pages</span>
                                    <NavLink to="/auth/signin-2" className="dud-logout" title="Logout"><i className="feather icon-log-out"/></NavLink>
                                </div>
                                <ul className="pro-body">
                                    <li><NavLink to="/auth/signin-2" className="dropdown-item"><i className="feather icon-settings"/> Settings</NavLink></li>
                                    <li><NavLink to="/Profile" className="dropdown-item"><i className="feather icon-user"/> Profile</NavLink></li>
                                    <li><NavLink to="/message" className="dropdown-item"><i className="feather icon-mail"/> My Messages</NavLink></li>
                                    <li><NavLink to="/auth/signin-2" className="dropdown-item"><i className="feather icon-lock"/> Lock Screen</NavLink></li>
                                </ul>

                            </Dropdown.Menu>
                        </Dropdown>
                    </li>
                </ul>
                <ChatList listOpen={this.state.listOpen} closed={() => {this.setState({listOpen: false});}} />
            </Aux>
        );
    }
}

export default NavRight;
